import React, { useState, useEffect, useRef } from 'react';
import {
  Typography,
  Box,
  TextField,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Alert,
  CircularProgress,
  Paper,
  Chip,
  Card,
  CardContent,
  Stack,
  Divider,
  Button,
  Tabs,
  Tab,
  IconButton,
  Grid,
  Container,
  AppBar,
  Toolbar,
  Drawer,
  Switch,
  FormControlLabel,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Pagination,
  Tooltip,
  Badge,
} from '@mui/material';
import {
  Article as ArticleIcon,
  Favorite,
  FavoriteBorder,
  Analytics,
  Cloud,
  Search,
  Refresh,
  FilterList,
  TrendingUp,
  OpenInNew,
  DarkMode,
  LightMode,
  AccessTime,
  Keyboard,
  Visibility,
} from '@mui/icons-material';
import { ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';

import { newsService } from './services/newsService';
import type { Article, KeywordStats } from './services/newsService';
import { KeywordCloud } from './components/KeywordCloud';
import { KeywordNetwork } from './components/KeywordNetwork';
import { useThemeProvider } from './hooks/useTheme';
import { useKeyboardShortcuts } from './hooks/useKeyboardShortcuts';
import { calculateReadingTime, formatReadingTime } from './utils/readingTime';


interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index } = props;
  return (
    <div role="tabpanel" hidden={value !== index}>
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

//     ( )
interface ArticleCardProps {
  article: Article;
  onToggleFavorite: (id: number) => void;
}

function ArticleCard({ article, onToggleFavorite }: ArticleCardProps) {
  const readingTime = calculateReadingTime((article.title || '') + (article.summary || ''));
  
  return (
    <Card sx={{ 
      mb: 2.5, 
      transition: 'all 0.3s ease-in-out', 
      '&:hover': { 
        transform: 'translateY(-2px)',
        boxShadow: '0 8px 25px rgba(0, 0, 0, 0.15)'
      },
      borderRadius: 3,
      overflow: 'hidden'
    }}>
      <CardContent sx={{ p: 3 }}>
        <Grid container spacing={2}>
          <Grid item xs={11}>
            <Box sx={{ display: 'flex', alignItems: 'flex-start', gap: 1.5, mb: 2 }}>
              <Box sx={{ flex: 1 }}>
                <Typography variant="h6" sx={{ 
                  fontWeight: 700, 
                  mb: 1.5,
                  lineHeight: 1.4,
                  fontSize: '1.15rem'
                }}>
                  <a href={article.link} target="_blank" rel="noopener noreferrer" 
                     style={{ 
                       textDecoration: 'none', 
                       color: 'inherit'
                     }}>
                    {article.title}
                    <OpenInNew fontSize="small" sx={{ ml: 1, verticalAlign: 'middle', opacity: 0.7 }} />
                  </a>
                </Typography>
              </Box>
            </Box>
            
            <Stack direction="row" spacing={2} sx={{ mb: 2, flexWrap: 'wrap', gap: 1 }}>
              <Chip
                icon={<ArticleIcon fontSize="small" />}
                label={article.source}
                variant="outlined"
                size="small"
                color="primary"
              />
              <Chip
                icon={<AccessTime fontSize="small" />}
                label={new Date(article.published).toLocaleDateString('ko-KR')}
                variant="outlined"
                size="small"
              />
              <Chip
                icon={<Visibility fontSize="small" />}
                label={formatReadingTime(readingTime)}
                variant="outlined"
                size="small"
                color="secondary"
              />
            </Stack>

            {article.summary && (
              <Typography variant="body1" sx={{ 
                mb: 2, 
                lineHeight: 1.7,
                color: 'text.secondary',
                fontSize: '0.95rem'
              }}>
                {article.summary.length > 200 
                  ? `${article.summary.substring(0, 200)}...` 
                  : article.summary}
              </Typography>
            )}

            {article.keywords && article.keywords.length > 0 && (
              <Box sx={{ mt: 2 }}>
                <Typography variant="body2" component="div" sx={{ mb: 1, fontWeight: 600 }}>
                   
                </Typography>
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                  {article.keywords.slice(0, 8).map((keyword, index) => (
                    <Chip 
                      key={index} 
                      label={keyword} 
                      size="small"
                      variant="outlined"
                      sx={{ 
                        fontSize: '0.75rem',
                        height: 24,
                        borderRadius: 3,
                        '&:hover': {
                          backgroundColor: 'primary.main',
                          color: 'primary.contrastText',
                          borderColor: 'primary.main'
                        }
                      }} 
                    />
                  ))}
                  {article.keywords.length > 8 && (
                    <Chip 
                      label={`+${article.keywords.length - 8}`}
                      size="small"
                      variant="filled"
                      color="default"
                      sx={{ fontSize: '0.75rem', height: 24 }}
                    />
                  )}
                </Box>
              </Box>
            )}
          </Grid>
          
          <Grid item xs={1}>
            <Stack spacing={1} alignItems="center">
              <Tooltip title={article.is_favorite ? ' ' : ' '}>
                <IconButton 
                  onClick={() => onToggleFavorite(article.id)}
                  color={article.is_favorite ? "secondary" : "default"}
                  sx={{
                    transition: 'all 0.2s',
                    '&:hover': {
                      transform: 'scale(1.1)'
                    }
                  }}
                >
                  {article.is_favorite ? <Favorite /> : <FavoriteBorder />}
                </IconButton>
              </Tooltip>
              <Typography variant="caption" color="text.secondary">
                #{article.id}
              </Typography>
            </Stack>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
}

//    
function KeyboardShortcutsHelp() {
  return (
    <Paper sx={{ p: 2, mb: 2, bgcolor: 'action.hover' }}>
      <Typography variant="body2" sx={{ mb: 1, fontWeight: 600 }}>
          
      </Typography>
      <Stack spacing={0.5}>
        <Typography variant="body2"> Ctrl/Cmd + R:  </Typography>
        <Typography variant="body2"> Ctrl/Cmd + D:  </Typography>
        <Typography variant="body2"> Ctrl/Cmd + K:  </Typography>
        <Typography variant="body2"> Ctrl/Cmd + /:  </Typography>
      </Stack>
    </Paper>
  );
}

//  App 
export default function App() {
  const { isDarkMode, toggleTheme, theme, ThemeContext } = useThemeProvider();
  const [tabValue, setTabValue] = useState(0);
  const searchInputRef = useRef<HTMLInputElement>(null);
  const [articles, setArticles] = useState<Article[]>([]);
  const [filteredArticles, setFilteredArticles] = useState<Article[]>([]);
  const [keywordStats, setKeywordStats] = useState<KeywordStats[]>([]);
  const [loading, setLoading] = useState(false);
  const [collecting, setCollecting] = useState(false);
  
  //  
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedSource, setSelectedSource] = useState('all');
  const [dateFrom, setDateFrom] = useState(() => {
    const date = new Date();
    date.setDate(date.getDate() - 7);
    return date.toISOString().split('T')[0];
  });
  const [dateTo, setDateTo] = useState(() => new Date().toISOString().split('T')[0]);
  const [favoritesOnly, setFavoritesOnly] = useState(false);
  
  // 
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;
  
  // 
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [showShortcutsHelp, setShowShortcutsHelp] = useState(false);

  //   
  useEffect(() => {
    const loadInitialData = async () => {
      setLoading(true);
      try {
        //     
        const existingArticles = newsService.getFilteredArticles({});
        if (existingArticles.length === 0) {
          //    
          await collectNews();
        } else {
          setArticles(existingArticles);
          updateKeywordStats();
        }
      } catch (error) {
        console.error('Failed to load initial data:', error);
      } finally {
        setLoading(false);
      }
    };

    loadInitialData();
  }, []);

  //  
  useEffect(() => {
    const filtered = newsService.getFilteredArticles({
      search: searchTerm,
      source: selectedSource === 'all' ? undefined : selectedSource,
      dateFrom: new Date(dateFrom),
      dateTo: new Date(dateTo),
      favoritesOnly,
    });
    setFilteredArticles(filtered);
    setCurrentPage(1);
  }, [articles, searchTerm, selectedSource, dateFrom, dateTo, favoritesOnly]);

  //  
  const collectNews = async () => {
    setCollecting(true);
    try {
      const newArticles = await newsService.collectNews();
      setArticles(newArticles);
      updateKeywordStats();
    } catch (error) {
      console.error('Failed to collect news:', error);
    } finally {
      setCollecting(false);
    }
  };

  //   
  const updateKeywordStats = () => {
    const stats = newsService.getKeywordStats();
    setKeywordStats(stats);
  };

  //  
  const handleToggleFavorite = (articleId: number) => {
    newsService.toggleFavorite(articleId);
    setArticles([...newsService.getFilteredArticles({})]);
  };

  //  
  const handleTabChange = (_: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  //  
  const focusSearch = () => {
    searchInputRef.current?.focus();
  };

  //   
  useKeyboardShortcuts({
    onRefresh: collectNews,
    onToggleTheme: toggleTheme,
    onSearch: () => searchInputRef.current?.focus(),
    onNextTab: () => setTabValue(prev => (prev + 1) % 4),
    onPrevTab: () => setTabValue(prev => (prev - 1 + 4) % 4),
  });

  //  
  const totalPages = Math.ceil(filteredArticles.length / itemsPerPage);
  const currentArticles = filteredArticles.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  //  
  const sources = newsService.getSources();
  const stats = newsService.getStats();

  return (
    <ThemeContext.Provider value={{ isDarkMode, toggleTheme, theme }}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
      
      {/*   */}
      <AppBar position="fixed" sx={{ zIndex: theme => theme.zIndex.drawer + 1 }}>
        <Toolbar>
          <Typography variant="h5" component="div" sx={{ flexGrow: 1, fontWeight: 'bold' }}>
             ~(News IT's Issue)
          </Typography>
          
          <Stack direction="row" spacing={1}>
            <Tooltip title=" ">
              <IconButton 
                color="inherit" 
                onClick={() => setShowShortcutsHelp(!showShortcutsHelp)}
              >
                <Keyboard />
              </IconButton>
            </Tooltip>
            
            <Tooltip title={isDarkMode ? ' ' : ' '}>
              <IconButton color="inherit" onClick={toggleTheme}>
                {isDarkMode ? <LightMode /> : <DarkMode />}
              </IconButton>
            </Tooltip>
            
            <Badge badgeContent={collecting ? '' : null} color="secondary">
              <Tooltip title="">
                <IconButton 
                  color="inherit" 
                  onClick={collectNews}
                  disabled={collecting}
                >
                  <Refresh />
                </IconButton>
              </Tooltip>
            </Badge>
            
            <Tooltip title="">
              <IconButton color="inherit" onClick={() => setDrawerOpen(!drawerOpen)}>
                <FilterList />
              </IconButton>
            </Tooltip>
          </Stack>
        </Toolbar>
      </AppBar>
      
      {/*  () */}
      <Drawer
        variant="persistent"
        open={drawerOpen}
        sx={{
          width: 300,
          flexShrink: 0,
          '& .MuiDrawer-paper': { width: 300, boxSizing: 'border-box', pt: 8 },
        }}
      >
        <Box sx={{ p: 2 }}>
          {showShortcutsHelp && <KeyboardShortcutsHelp />}
          
          <Typography variant="h6" gutterBottom> </Typography>
          
          {/*   */}
          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel> </InputLabel>
            <Select
              value={selectedSource}
              onChange={(e) => setSelectedSource(e.target.value)}
              label=" "
            >
              <MenuItem value="all"></MenuItem>
              {sources.map(source => (
                <MenuItem key={source} value={source}>{source}</MenuItem>
              ))}
            </Select>
          </FormControl>

          {/*   */}
          <TextField
            fullWidth
            inputRef={searchInputRef}
            label=" "
            placeholder=": AI, , 5G (Ctrl+K)"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            sx={{ mb: 2 }}
            InputProps={{
              startAdornment: <Search sx={{ mr: 1, color: 'text.secondary' }} />
            }}
          />

          {/*   */}
          <TextField
            fullWidth
            type="date"
            label=""
            value={dateFrom}
            onChange={(e) => setDateFrom(e.target.value)}
            sx={{ mb: 2 }}
            InputLabelProps={{ shrink: true }}
          />
          
          <TextField
            fullWidth
            type="date"
            label=""
            value={dateTo}
            onChange={(e) => setDateTo(e.target.value)}
            sx={{ mb: 2 }}
            InputLabelProps={{ shrink: true }}
          />

          {/*   */}
          <FormControlLabel
            control={
              <Switch
                checked={favoritesOnly}
                onChange={(e) => setFavoritesOnly(e.target.checked)}
              />
            }
            label=" "
            sx={{ mb: 2 }}
          />

          <Divider sx={{ my: 2 }} />
          
          {/*   */}
          <Typography variant="h6" gutterBottom>  </Typography>
          
          <Button
            variant="contained"
            fullWidth
            startIcon={collecting ? <CircularProgress size={20} /> : <Refresh />}
            onClick={collectNews}
            disabled={collecting}
            sx={{ mb: 2 }}
          >
            {collecting ? ' ...' : '  '}
          </Button>

          {/*  */}
          <Paper sx={{ p: 2, bgcolor: 'grey.50' }}>
            <Typography variant="body2">
                {stats.totalArticles} <br/>
               {stats.totalSources} <br/>
               {stats.totalFavorites} <br/>
                7: {stats.recentArticles}
            </Typography>
          </Paper>
        </Box>
      </Drawer>

      {/*   */}
      <Box sx={{ 
        flexGrow: 1, 
        p: 3, 
        pt: 12,
        ml: drawerOpen ? '300px' : 0,
        transition: 'margin-left 0.3s'
      }}>
        <Typography variant="body1" sx={{ mb: 2, color: 'text.secondary' }}>
          **IT/  , ,  **
        </Typography>

        {/*  */}
        <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 2 }}>
          <Tabs value={tabValue} onChange={handleTabChange}>
            <Tab icon={<ArticleIcon />} label="  " />
            <Tab icon={<Analytics />} label="  " />
            <Tab icon={<Cloud />} label=" " />
            <Tab icon={<Favorite />} label=" " />
          </Tabs>
        </Box>

        {loading && (
          <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
            <CircularProgress />
          </Box>
        )}

        {/*    */}
        <TabPanel value={tabValue} index={0}>
          <Typography variant="h5" gutterBottom>  </Typography>
          <Typography variant="body1" sx={{ mb: 2, fontWeight: 'bold' }}>
            ** {filteredArticles.length} **
          </Typography>

          {filteredArticles.length === 0 ? (
            <Alert severity="info">
              {articles.length === 0 ? 
                ' .  " "    .' :
                '    .'
              }
            </Alert>
          ) : (
            <>
              {currentArticles.map(article => (
                <ArticleCard
                  key={article.id}
                  article={article}
                  onToggleFavorite={handleToggleFavorite}
                />
              ))}
              
              {totalPages > 1 && (
                <Box sx={{ display: 'flex', justifyContent: 'center', mt: 3 }}>
                  <Pagination
                    count={totalPages}
                    page={currentPage}
                    onChange={(_, page) => setCurrentPage(page)}
                    color="primary"
                  />
                </Box>
              )}
            </>
          )}
        </TabPanel>

        {/*    */}
        <TabPanel value={tabValue} index={1}>
          <Typography variant="h5" gutterBottom>   </Typography>
          
          {keywordStats.length === 0 ? (
            <Alert severity="info">  .</Alert>
          ) : (
            <Grid container spacing={3}>
              <Grid item xs={12} md={6}>
                <Typography variant="h6" gutterBottom>   TOP 20</Typography>
                <Paper sx={{ p: 2, maxHeight: 400, overflow: 'auto' }}>
                  <List dense>
                    {keywordStats.slice(0, 20).map((stat, index) => (
                      <ListItem key={stat.keyword}>
                        <ListItemText
                          primary={`${index + 1}. ${stat.keyword}`}
                          secondary={`${stat.count}`}
                        />
                      </ListItem>
                    ))}
                  </List>
                </Paper>
              </Grid>
              
              <Grid item xs={12} md={6}>
                <Typography variant="h6" gutterBottom>  </Typography>
                <Paper sx={{ p: 2, height: 400 }}>
                  {keywordStats.length > 0 && (
                    <KeywordCloud keywords={keywordStats.slice(0, 50)} />
                  )}
                </Paper>
              </Grid>

              <Grid item xs={12}>
                <Typography variant="h6" gutterBottom>   </Typography>
                <Paper sx={{ p: 2, height: 500 }}>
                  <KeywordNetwork data={newsService.getKeywordNetwork()} />
                </Paper>
              </Grid>
            </Grid>
          )}
        </TabPanel>

        {/*   */}
        <TabPanel value={tabValue} index={2}>
          <Typography variant="h5" gutterBottom> </Typography>
          
          {keywordStats.length === 0 ? (
            <Alert severity="info">   .</Alert>
          ) : (
            <Paper sx={{ p: 2, height: 600 }}>
              <KeywordCloud keywords={keywordStats} />
            </Paper>
          )}
        </TabPanel>

        {/*   */}
        <TabPanel value={tabValue} index={3}>
          <Typography variant="h5" gutterBottom> </Typography>
          
          {(() => {
            const favorites = articles.filter(a => a.is_favorite);
            return favorites.length === 0 ? (
              <Alert severity="info">  .</Alert>
            ) : (
              <>
                <Typography variant="body1" sx={{ mb: 2, fontWeight: 'bold' }}>
                  ** {favorites.length} **
                </Typography>
                {favorites.map(article => (
                  <ArticleCard
                    key={article.id}
                    article={article}
                    onToggleFavorite={handleToggleFavorite}
                  />
                ))}
              </>
            );
          })()}
        </TabPanel>
      </Box>
      </ThemeProvider>
    </ThemeContext.Provider>
  );
}